*,*::after,*::before{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
:root{
    --brand-color:#43ef30;
    --dark-bg1:rgb(32,32,32);
    --blk-btn-bg-color:#273239;
    --blk-heading-color:rgba(0,0,0,.68);
}
html{
    scroll-behavior: smooth;
}
body{
    font-family:sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue';
    -webkit-font-smoothing: antialiased;
    
}

.body-container{
    /* background-color: blue; */
    width: 80%;
    margin:1rem auto 0 auto;
    padding-top: 13vh;
}
.blk-color{
    color:var(--blk-heading-color);
}

/* for Nav LInk  */
.activeClass{
    color: #fff !important;
   background-color:  rgb(39, 48, 54) !important;
}


.totop{
    height: 40px;
    width: 40px;
    background-color:#43ef30 ;
    position: fixed;
    bottom:5vh;
    right: 5vw;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
    display: grid;
    place-items: center;
    color: white;
}
.totop img{
    height: 35px;
}


@media screen and (max-width:768px) {
    .body-container{
        width: 90%;
    }
}