.mobilenav {
  margin-top: 10vh;
  height: 87vh;
  width: 393px;
  max-width: 100vw;
  right: 0;
  background-color: rgb(241, 241, 241);
  position: fixed;
  z-index: 10;
  transform: translateX(100%);
  transition: all 0.3s ease;
}

.mobilenavactive {
  transform: translateX(0%);
}

.authbox {
  min-height: 100px;
  width: 100%;
  box-shadow: rgb(0, 0, 0, 0.11) 0 1px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.authbox a {
  border-radius: 5px;
  background-color: #273239;
  width: 150px;
  padding: 1rem;
  text-decoration: none;
  color: #ffffff;
  text-align: center;
}
.authbox .userwrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  padding: 1rem;
}
.authbox .userwrapper a {
  margin-top: 1rem;
  width: 380px;
}
.authbox .userwrapper .fetched {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: left;
  background: transparent !important;
  color: rgba(0, 0, 0, 0.84);
}
.authbox .userwrapper .fetched img {
  height: 50px;
  margin: 0 2rem;
}
.authbox .userwrapper .fetched span {
  font-size: large;
}

.authbox ul {
  display: flex;
  flex-direction: column;
  width: 100%;
  /* text-align: left; */
  list-style-type: none;
  align-items: center;
  justify-content: left;
}
.authbox ul a {
  background-color: transparent;
  color: black;
  /* background-color: blanchedalmond; */
  border-bottom: 1px solid rgba(0, 0, 0, 0.84);
  width:100%;
}
.authbox ul a:hover{
  background-color: chartreuse;
}
