.readblogcontainer{
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    width: 100%;
    /* background-color:pink; */
    padding: 1rem;
    min-height: 80vh;
    border-radius: 5px;
    border:1px solid rgba(0,0,0,.125);
}

h1{
    margin-top: 1rem !important;
    padding: 0.5rem;
    word-wrap: break-word;
    color: rgba(0,0,0,.68);
}
.authorname{
    padding: 0.5rem;
    color: rgba(0,0,0,.68);
}
.imagediv {
    width: 100%;
    /* background: brown; */
    display: grid;
    margin: 1rem 0 2rem 0;
}
.imagediv .coverimg{
    border-radius: 5px;
    height: fit-content;
    max-width: 95%;
    object-fit: cover;
    margin: 0 auto;
}

.foldpaperdate {
    text-align: center;
    line-height: 25px;
    color: #fff;
    font-size: 12px;
    margin-top: -26px;
    position: relative;
    padding: 0 7px;
    float: left;
    background-color: rgb(52, 218, 34)
}
.trianglepaper {
    border-color: transparent transparent #2ba71d transparent;
}
.trianglepaper {
    position: absolute;
    left: -10px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 10px 10px;
    border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #43ef30 rgba(0, 0, 0, 0);
}


.articlediv{
    padding: 0.5rem;
    color:#333333;
}

.blogContent{
    padding: 0.5rem;
    color:#333333;
    line-height: 1.7;
    font-size: large;
    word-wrap: break-word;
}