.module{
    position:absolute;
    height:100%;
    width:100%;
    background-color: rgb(255, 255, 255); 
    padding: 1rem;
    transition: all 0.3s ease-in-out;
    animation: down 0.3s linear forwards ;
    overflow-y: scroll;
    transform: translate(-1rem);
}
@keyframes down {
    0%{
        bottom:-100%;
    }
    100%{
        bottom:0;
    }
}
.module img{
    position: fixed;
    height: 30px;
    cursor: pointer;
    transition: all 0.3s ease-in;
}

.module .formdiv{
    margin-top: 2rem;

    /* background: turquoise; */
    padding: 1rem 0.5rem;

}
.module .formdiv .formchild{
    width: 100%;
    /* background: greenyellow; */
    margin: 1rem 0;
    padding:0.5rem 1rem;
    display: flex;
    flex-direction: column;
}
.module .formdiv .formchild input{
    padding:0.8rem  1rem;
    border-radius: 8px;
    outline: none;
    border:2px solid rgba(0,0,0,0.125);
}
.module .formdiv .formchild select{
    padding: 1rem;
}

.module .formdiv .formchild button{
    width: 100%;
    padding: 1rem;
    cursor: pointer;
}













.module::-webkit-scrollbar {
    width: 10px;
  }

  .module::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
.module::-webkit-scrollbar-thumb {
    background: #888; 
  }
  .module::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }