.dashboardcontainer{
    min-height: 80vh;
    width: 100%;
    border: 1px solid rgba(0,0,0,0.125);
    border-radius: 8px;
    z-index: 4;
    /* display: grid;
    grid-template-columns: repeat(12,1fr); */
    display: flex;
    overflow: hidden;
    position: relative;

}

/* rgb(242,242,242) */
.dashnav{
    max-width: 200px !important;
    width: 100%;
    border-right: 1px solid rgba(0,0,0,0.125);
    min-height:80vh;
    max-height: 100%;
    background-color: rgb(54,73,86);
    z-index: 3;
    grid-column: span 2;
    transition: all 0.3s ease;
   
}

.navToggle{
    position: absolute;
    transform: translateX(-100%);
}

.dashnav ul{
    width:200px !important;
    display: flex;
    flex-direction: column;
    align-items: right;
    justify-content:right;
    list-style-type: none;
    /* padding-top: 1rem; */
}
.dashnav ul  li{
    height:30px;
    width:200px !important;
    height: 100%;
    display:grid;
    place-items: center;
    /* margin-bottom: 0.5rem; */
}
.dashnav ul li a{
    width:200px;
    height: 100%;
    text-decoration: none;
    padding:0.5rem 2.5rem;
    /* background-color: rgb(232, 238, 230); */
    color: rgba(255, 255, 255, 0.76);
    transition: all 0.3s ease-in-out;
}
.dashnav ul li a:hover{
    background-color: #273036;
    color: rgba(255, 255, 255, 0.76) ;
}
.link{
    background-color: #43ef30da !important;
    color: #fff !important;
}

/* dash right css  */
.dashright{
    width:100%;
    max-width: 100%;
    /* background-color: rgba(0,0,0,0.125); */
    /* padding:1rem; */
    transition: all 0.3s ease;
}

.dashburger{
    height: 8vh;
    /* background-color: rosybrown; */
    display: flex;
    align-items: center;
    padding:0 1rem;
}
.dashburger img{
    height: 30px;
    cursor: pointer;
}
.rightcontent{
    display: flex;
    padding:1rem;
}


.formblock{
    height: auto;
    width: 100%;
    padding: 1rem;
}

.myeditor{
    display: flex !important;
    align-items:left !important;
    flex-direction: column;
    justify-content: center;
}

textarea{
    resize: none;
    height:500px;
    width: 100%;
    padding: 1rem;
    outline: none;
    border-radius: 5px;
    font-size: large;
}

.submitbtn{
    cursor: pointer;
    padding: 1rem;
    width: 100%;
}


