.latestbox{
    height: auto;
    padding: 0.5rem;
    display: flex;
    margin: 0 auto;
    text-decoration: none;
    transition: all 0.3s ease;
    border-bottom: 1px solid black;
    margin-bottom: 1rem;
    border:1px solid rgba(0,0,0,.125);
}

.articleimg{
    width:60%;
    overflow:hidden;
}
.articleimg img{
    max-width: 100%;
    max-height: 100%;
    transition: all 0.5s ease-in-out;
}
.articleimg:hover img{
    transform: scale(1.5);
}

.articletxt{
    width: 40%;
    display: flex;
    flex-direction: column;
    padding: 1rem 2rem;
    word-wrap: break-word;
    /* color:rgb(41,41,41) !important; */
}
.articletxt h1{
    color: rgb(41,41,41);
    margin-bottom: 1rem;
}
.articletxt h2{
    color: rgb(117,117,117);
    font-size:medium;
}

.articletxt .authorbox{
    margin-top: 0.6rem;
    height: 80px;
    /* background-color: violet; */
    display: flex;
    align-items: center;
}
.authorbox .authorimg img{
    height: 50px;
    width: 50px;
    border-radius: 50%;
    overflow: hidden;
}

.authornameandarticledate{
    margin-left: 0.5rem;
    display: flex;
    flex-direction: column;
    font-size:medium;
}
.authorname{
    color:#43ef30;
}
.articledate{
    font-size: small;
    font-weight: bold;
}

@media screen and (max-width:768px) {
    .latestbox{
        flex-direction: column;
    }
    .articleimg{
        width:100%;
        margin-bottom: 1rem;
    }
    .articletxt{
        width: 100%;
        padding:0;
    }
    .articletxt h1{
        font-size: x-large;
        /* line-height: 1.7; */
        margin-bottom: 0;
    }
    .articletxt h2{
        line-height: 1.7;
        letter-spacing:0.5px;
    }
}
