.container{
    height: 80%;
    width:70%;
    /* background-color: black; */
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.container h1{
    margin: 1rem 0;
}
.container img{
    margin: 1rem 0;
}

.homeandlogin{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.homeandlogin a{
    height: 8vh;
    text-transform: uppercase;
    text-decoration: none;
    padding: 1rem;
    text-align: center;
}
.homeandlogin a:nth-child(1){
    width: 300px;
    background-color:#43ef30;
    color: whitesmoke;
    border-radius: 5px;
    display: grid;
    place-items: center;
    font-size: large;
    transition: all 0.3s ease-in-out;
}
.homeandlogin a:nth-child(1):hover{
    background-color: #273239;
}