.mainHeader{
    height:8vh;
    width: 100%;
    box-shadow: rgb(0, 0, 0 ,0.11) 0 1px;
    display:grid;
    grid-template-columns: repeat(12,1fr);
    position: fixed;
    z-index: 11;
    background-color: #fff;
    top: 0;
}

.logo{
    grid-column:span 3;
    display: flex;
    align-items: center;
    justify-content:flex-start;
    margin-left: 2rem;
    height:100%;
    width: auto;
}
.logo img{
    height: 5vh;
    width: auto;
}
.searchbox{
    grid-column: span 6;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.userbox{
    grid-column:span 3;
    display: flex;
    align-items: center;
    justify-content:flex-end;
    margin-right: 2rem;
}
.userbox a{
    text-transform: uppercase;
    text-decoration: none;
    color: inherit;
    margin:0 0.5rem;
    font-size:14px;
    /* font-weight: bold; */
    transition: all 0.1s ease;
}
.userbox .signupbtn:hover{
    color: #43ef30;
}
.userbox .signinbtn{
    background-color: #273239;
    padding: 8px 27px;
    border-radius: 5px;
    color: #fff;
}
.afterlogin{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.afterlogin img{
    margin-right: 0.4rem;
}
.afterlogin span{
    margin-right: 0.5rem;
}
nav{
    width: 100%;
    height: 5vh;
    background-color: rgb(54,73,86)!important;
    position: fixed;
    z-index: 11;
    background-color: #fff;
    top: 8vh;
}

nav ul{
    width: 80%;
    height: 100%;
    margin: 0 auto;
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content:left;
    overflow-y: hidden;
    overflow-x: visible;
}
nav ul::-webkit-scrollbar {
    display: none;
  }

nav ul li{
    height: 100%;
    display:grid;
    place-items: center;
}
nav ul li a{
    height: 100%;
    padding:0 1rem;
    text-transform: uppercase;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.76);
    display: flex;
    align-items: center;
    justify-content: space-around;
    transition: all 0.3s ease-in-out;
    font-size: 14px;
}
nav ul li a:hover{
    background-color: #43ef30bd;
    color: #fff !important;
}
.activeClass{
    color: #fff !important;
   background-color:  rgb(46,62,73) !important;
}
.bars{
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.bars img{
    height: 35px;
}


@media screen and ( max-width:1200px){
    .searchbox{
        display: none;
        
    }
    .logo,.userbox{
        grid-column:span 6;
    }
}
@media screen and (max-width:863px){
    .searchbox{
        display: none;
    }
    .logo{
        grid-column:span 10;
    }
    
    
    nav ul{
        width: 90%;
    }
    /* .logo img{
        height: 5vh;
        width: auto;
    } */
}