.myblogsContainer{
    display: flex;
    width: 100%;
    /* align-items: center; */
    flex-direction: column;
    justify-content:left;
}
.bloglistcontainer{
    /* background-color: blanchedalmond; */
    display: grid;
    grid-template-columns: repeat(12,1fr);
    margin-bottom: 1rem;
    padding: 1rem;
}
.bloglistcontainer .blogdatabox{
    grid-column: span 8;
    display: flex;
    flex-direction: column;
}
.bloglistcontainer button{
    grid-column: span 2;
    padding: 0.5rem 1rem;
    cursor: pointer;
    margin: 0 0.5rem;
}


/* for my account  */
.myaccountcontainer{
    padding: 1rem;
    min-height: 300px;
    width: 100%;
    border: 1px solid rgba(0,0,0,0.125);
    margin:0 1rem;
    
}
.totalBlogs{
    padding: 1rem;
    background-color: rgba(0, 0, 0, 0.125);
    width: max-content;
    display: flex;
    flex-direction: column;
}
.userDetails{
    height: max-content;
    display: flex;
    flex-direction: column;
    padding: 1rem;

}
.userDetails .userdetailbox{
    padding:1rem;
}

