.bth{
    height: 8vh;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
    display: flex;
    align-items: center;
    color: #030303;
}

.signupcontainer{
    margin:0 auto;
    margin-top:0.5rem;
}

.signupbox{
    max-width: 95%;
    width: 600px;
    background-color: rgb(255, 255, 255);
    box-shadow:0 0 10px rgba(0,0,0,0.21) ;
    margin: 0 auto;
    border-radius: 8px;
    padding: 1.5rem 2rem;
}
.formControl{
    width: 100%;
    height:auto;
    /* background-color: yellow; */
    margin-bottom: 0.5rem;
}

.formControl:nth-child(1){
    font-size:0.8rem;
    font-weight: 3rem;
    color: rgba(0, 0, 0, 0.897);
    margin-bottom: 2rem;
}
.formControl:nth-child(2),.formControl:nth-child(3){
    margin-bottom: 1rem;
}
.formControl label{
    margin-bottom: 1rem;
    font-size:1rem;
    color: rgba(0,0,0,.68);
    font-weight: bold;
}

.formControl input{
    width: 100%;
    padding: 1rem;
    font-size: medium;
    /* background-color: rgba(128, 255, 0, 0.301); */
    outline: none;
    border: none;
    border: 1px solid rgba(0,0,0,.68) ;
    border-radius: 5px;
}
.formControl button{
    width: 100%;
    padding: 1rem;
    background-color:#273239;
    font-size: large;
    color: rgb(168, 168, 168);
    outline: none;
    border: none;
    border-radius: 5px;
    transition: all 0.3s ease-in-out;
}
.formControl button:hover{
    cursor: pointer;
    background-color: #1a1d1f;
  /* 2px 2px 20px  */
}

.formControl:nth-child(5){
    display: block;
}
.formControl:nth-child(6){
    margin-top: 1rem;
}

.formControl:nth-child(7){
 margin-top: 1rem;
    display: grid;
    place-items: center;
}
.formControl:nth-child(7) a{
    text-decoration: none;
    color:#43ef30;
}

.errorBorder{
    background-color:rgba(255, 0, 0, 0.301);
}
.successBorder{
    background-color:rgba(43, 255, 0, 0.301);
}
.submitok{
    cursor: pointer !important;
    color:#ffffff !important;
    
}

