footer {
  min-height: 50vh;
  width: 100%;
  margin-top: 5rem;
  /* background-color: rgb(32, 36, 39); */
}

.fsocial {
  background-color: rgba(9, 161, 9, 0.568);
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: rgb(237, 240, 246);
  padding: 0 30px;
}

.fsocial ul {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.fsocial ul a {
  margin: 0 20px;
}
.fsocial ul a li i {
  font-size: x-large;
  color: white;
  transition: all 0.3s ease;
}

.fsocial ul a li i:hover {
  color: rgb(30, 34, 31);
}

.fnav {
  background-color: rgb(63, 68, 80);
  min-height: 40vh;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  color: rgb(255, 255, 255);
  padding: 50px 30px;
}

.navelement {
  display: flex;
  flex-direction: column;
  align-items: left;
}
.navelement:nth-child(1) {
  grid-column: span 5;
}
.navelement:nth-child(1) div {
  line-height: 1.7;
  padding: 0 5rem 0 0;
}
.navelement span {
  font-size: large;
  width: max-content;
  text-transform: uppercase;
  font-weight: bold;
  padding: 0.8rem 0;
  border-bottom: 2px solid rgba(255, 255, 255, 0.568);
  margin-bottom:50px;
}
.navelement ul{
    display:flex;
    flex-direction: column;
    list-style-type: none;
    color: whitesmoke;
}
.navelement ul a{
    text-decoration: none;
    color: white;
    margin-bottom: 1rem;
    transition: all 0.3s ease;
}
.navelement ul a:hover{
    color: yellowgreen;
}

.navelement:nth-child(2),.navelement:nth-child(3),.navelement:nth-child(4){
    grid-column: span 2;
}
.navelement:nth-child(4) ul li{
    margin-bottom: 1rem;
}
.fcredit {
  height: 8vh;
  background-color: rgb(46, 49, 54);
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(237, 240, 246);
  padding: 0 5%;
}
.fcredit a img {
  max-height: 30px;
}

@media screen and (max-width: 768px) {
    footer{
        max-height: 600px;
    }
  .fsocial {
    height: auto;
    padding: 1rem;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .fsocial span {
    margin: 1rem 0;
  }

  .fnav {
    /* max-height: 600px; */
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(4, 1fr);
  }
  .navelement:nth-child(3){
      grid-column: span 5;
  }

  .navelement:nth-child(1) div {
    line-height: 1.7;
    padding: 0 0rem 0 0;
    margin-bottom: 1rem;
  }
}
