.blogcontainer {
    width: 100%;
    min-height:73vh;
    height:auto;
    /* background-color: rgb(150, 115, 115); */
    display: grid;
    grid-template-columns: repeat(12,1fr);
    /* grid */
}

.loadingcontainer{
    height: 100%;
    width: 70vw;
    /* grid-column: span 12; */
    /* display: grid;
    place-items: center; */
}
.prevnextbtns{
    height: 10vh;
    width: 100%;
    /* background-color: burlywood; */
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.prevnextbtns button{
    /* margin: 0 3rem; */
    outline: none;
    cursor: pointer;
    border:1px solid rgba(0,0,0,.125);
    padding: 0.8rem 3rem;
    color: #fff;
    background-color: #273239;
    border-radius: 8px;
}
.disabledbtn{
    background-color: #27323993 !important;
    cursor:default !important;
}

.smallbox{
    min-height:250px;
    max-height:300px;
    grid-column: span 4;
    border:1px solid rgba(0,0,0,.125);
    margin: 1rem;
    border-radius: 8px;
    padding-bottom: 1rem;
    transition: all 0.5s ease-in-out;
}
.smallbox a{
    text-decoration: none;
    color: #1e1e1e;
}
.smallimagediv{
    /* background-color: blueviolet; */
    height: 200px;
    overflow: hidden;
    border-radius: 8px 8px 0 0;
    
}
.smallimagediv img{
    height:200px !important;
    /* background-color: blueviolet; */
    width: 100%;
    transition: all 0.3s ease-in-out;
    object-fit: cover;
}
.smallimagediv:hover img{
    transform: scale(1.5);
    /* background-color: brown; */
}

.smallarticletitle{
    padding: 0.8rem 1rem 0 0.8rem;
    font-size: large;
    font-weight: bold;
    color:rgba(0,0,0,.68) ;
}

.smallarticledescription{
    padding:0.5rem 1rem;
    font-size: small;
    color: #273239;
}

@media (min-width:601px) and (max-width:950px) {
    .blogcontainer{
        grid-template-columns: repeat(8,1fr);
    }
    .smallbox{
        grid-column:span
         4;
    }
}


@media screen and (max-width:600px) {
    .blogcontainer{
        grid-template-columns: repeat(4,1fr);
    }

}